export default (inv) => {
    var data = JSON.parse(localStorage.getItem('ficha_'));
    const inputs = document.querySelectorAll('.input-form');
 
    if (!data[inv]){
        data[inv] = [];
    }

    var info = data;

    for (var i of data[inv]){
        if (inputs[0].value == i.name){
            inputs[0].value = inputs[0].value+" copia";
        }
    }

    var json = {
        name: inputs[0].value,
    };

    for (var c = 0; c < inputs.length; c++){
        json[(inputs[c].name).replaceAll('it_', '')] = inputs[c].value || undefined;
    }
    if (document.getElementsByClassName('textarea-form')[0]){
        json['desc'] = document.getElementsByClassName('textarea-form')[0].value;
    }
    
    info[inv].push(json);

    info.gasta_tudo = parseFloat(data.gasta_tudo) + (parseFloat(json.heavy) || 0);
    
    if (data.characterInfo.INVENTARIO_MAXIMO == undefined || (parseFloat(json.heavy) < 0)){
        return [true, 1]
    }

    if (info.gasta_tudo > Number(data.characterInfo.INVENTARIO_MAXIMO)){
        return [true, 0];
    }

    localStorage.setItem(`ficha_`, JSON.stringify(info));
    return window.location.reload();
} 